.Pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.Item {
  width: auto;
  min-width: 2em;
  height: 2em;
  line-height: 2em;
  margin: 1em .25em 1em 0;
  padding: 0 .25em;

  border-radius: .2em;
  border: 0;
  box-shadow: 0 0 0 1px #c1c1c1;

  cursor: pointer;
  text-align: center;

  outline: none;

  &:hover {
    background: rgba(0, 158, 107, .5);
    color: #fff;
    box-shadow: 0 0 0 1px rgba(0, 158, 107, .5);
  }
}
.Active {
  background: rgb(0, 158, 107);
  cursor: default;
  color: #fff;
  box-shadow: 0 0 0 1px rgb(0, 158, 107);
}
.Disabled {
  cursor: default;
  opacity: .5;
  color: #555;
  background: #f1f1f1;
  box-shadow: 0 0 0 1px #f1f1f1;
}
.Dots {
  width: auto;
  min-width: 1em;
  height: 2em;
  line-height: 2em;
  margin: 1em .25em 1em 0;
  padding: 0 .25em;

  border: 0;

  text-align: center;
}
