@import "../../data";

.Users {
  padding: 0;
}
.loading {
  opacity: .5;
}
.title {
  display: block;
  margin-bottom: .5em;
  font-size: 1.5em;
}

.list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0 0 5em;
  padding: 0;
}

.item {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 0 0 .2em;
  padding: .5em .25em;
  border-radius: .2em;
  box-shadow: 1px 1px 1px darken($gray-color, 1), 1px 1px 1px lighten($gray-color, 20) inset;
  background: lighten($gray-color, 19);

  .UserName {
    width: 100%;
    padding: .75em 0;

    h3 {
      margin: 0;
    }

    .Status {
      line-height: 1em;
      font-size: 90%;
    }
    .StatusIcon {
      margin: 0 .2em;
      width: 1.5em;
      height: 1.5em;
      transform: translateY(-3px);
      fill: $main-color;
    }
  }
}
.avaWrapper {
  width: 8em;
  text-align: center;
}
.ava {
  display: block;
  width: 5em;
  margin: 0 auto 0.2rem;
  padding: .5em;
  box-sizing: border-box;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
}
.avaLink {
  display: block;
  text-align: center;
  height: 80px;
}

.btn {
  padding: .5em 1em;
  border: 0;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background: $main-color;
  cursor: pointer;
  outline: none;

  transition: all .15s ease-in-out;

  &:disabled {
    opacity: .2;
  }
}


.follow {
  background: $warning-color;
  &:hover {
    background: darken($warning-color, 10);
  }
}
.unfollow {
  background: $main-color;
  &:hover {
    background: darken($main-color, 10);
  }
}
.hidden {
  display: none;
}
.location {
  width: 8em;
  text-align: left;
}
